ter;\n    border: none;\n    outline: none;\n    cursor: pointer;\n\n    img {\n        width: 24px;\n        height: 24px;\n        min-width: 22px;\n    }\n}\n\n.login__h2 {\n    font-family: $ff-bold;\n    font-style: normal;\n    font-weight: 700;\n    font-size: 32px;\n    line-height: 38px;\n    color: rgba(255, 255, 255, 0.87);\n    margin-top: 42px;\n}\n\n.login__form {\n    margin-top: 50px;\n}\n\n.login__wrap-field {\n    margin-bottom: 25px;\n\n    .login__label {\n        display: block;\n        margin-bottom: 8px;\n        font-style: normal;\n        font-weight: 400;\n        font-size: 16px;\n        line-height: 150.5%;\n        color: rgba(255, 255, 255, 0.87);\n    }\n\n    .login__input {\n        box-sizing: border-box;\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n        padding: 12px;\n        gap: 10px;\n        width: 327px;\n        height: 48px;\n        background: #fff;\n        border: 0.8px solid #979797;\n        border-radius: 4px;\n        color: $color-bg-item;\n    }\n}\n\n.login__or {\n    width: 327px;\n    height: 24px;\n    font-style: normal;\n    font-weight: 400;\n    font-size: 16px;\n    line-height: 150.5%;\n    color: #979797;\n    position: relative;\n    text-align: center;\n    margin-top: 26px;\n\n    &::after {\n        content: '';\n        display: inline-block;\n        width: 150px;\n        height: 1px;\n        background: #979797;\n        position: absolute;\n        top: 13px;\n        left: 175px;\n    }\n\n    &::before {\n        content: '';\n        display: inline-block;\n        width: 150px;\n        height: 1px;\n        background: #979797;\n        position: absolute;\n        top: 13px;\n        right: 175px;\n    }\n}\n\n.login__btn-login {\n    display: flex;\n    flex-direction: row;\n    justify-content: center;\n    align-items: center;\n    padding: 12px 24px;\n    background: rgba(134, 135, 231, 0.5);\n    border-radius: 4px;\n    font-style: normal;\n    font-weight: 400;\n    font-size: 16px;\n    line-height: 150.5%;\n    color: rgba(255, 255, 255, 0.5);\n    width: 327px;\n    max-height: 48px;\n    transition: all .3s;\n\n    &:hover {\n        color: #fff;\n        transition: all .3s;\n        background: #8875FF;\n        cursor: pointer;\n    }\n}\n\n.login__btn-login-google {\n    box-sizing: border-box;\n    display: flex;\n    flex-direction: row;\n    justify-content: center;\n    align-items: center;\n    padding: 12px 24px;\n    width: 327px;\n    max-height: 48px;\n    border: 1px solid #8875FF;\n    border-radius: 4px;\n    background: transparent;\n    margin-top: 29px;\n    font-style: normal;\n    font-weight: 400;\n    font-size: 16px;\n    line-height: 150.5%;\n    color: rgba(255, 255, 255, 0.5);\n    transition: all .3s;\n    overflow: hidden;\n\n    .wrap_img {\n        height: auto;\n        width: auto;\n        margin: initial;\n        margin-right: 10px;\n\n        img {\n            margin: initial;\n            height: auto;\n            width: auto;\n            transform: translateY(4px);\n        }\n    }\n\n    span {\n        white-space: nowrap;\n    }\n\n    &:hover {\n        background: #8875FF;\n        transition: all .3s;\n        cursor: pointer;\n        color: #fff;\n    }\n}\n\n.login__links {\n    display: flex;\n    justify-content: space-between;\n    max-width: 327px;\n    margin-top: 46px;\n\n    .login__link {\n        text-decoration: none;\n        color: rgba(255, 255, 255, 0.5);\n        transition: all .3s;\n        font-weight: 100;\n        font-family: $ff-light;\n\n        &:hover {\n            transition: all .3s;\n            cursor: pointer;\n            color: #fff;\n        }\n    }\n}\n\n.text-danger {\n    color: $red;\n    font-size: 12px;\n}\n","// Body\n$body-bg: #121212;\n\n// Typography\n@import url('https://fonts.googleapis.com/css?family=Lato');\n$ff-regular: 'Lato regular', sans-serif;\n$ff-light: 'Lato light', sans-serif;\n$ff-bo