    </div>
</template>

<script>

export default {
    name: "SignUp",
    components: {},
    data: function () {
        return {
            email: '',
            password: '',
            password_confirmation: '',
            errors: {},
            user: [],
            locale: 'en'
        }
    },
    methods: {
        async handleSignUp() {
            const {email, password, password_confirmation, locale} = this;
            let result = await this.$auth.register({
                data: {email, password, password_confirmation, locale},
                success() {
                    this.$auth.ready();
                },
                staySignedIn: true,
                redirect: {name: '/home'},
            }).catch(e => this.errors = e.response.data.errors)
            if(result.status === 200) {
                this.$router.push('/home')
            }
        },
        handleSignUpGoogle () {

        },
        setLocale(locale) {
            if (locale === 'English') {
                this.$i18n.locale = 'en'
                this.locale = 'en'
            } else {
                this.$i18n.locale = 'ru'
                this.locale = 'ru'
            }
        }
    }
}
</script>

